@import "../../assets/style/variable.scss";

section {
  padding-bottom: 70px;
  ul {
    width: 250px;
    display: flex;
    justify-content: space-between;
  }
  .itemName {
    li {
      &:nth-child(1) {
        width: 150px;
        font-size: 14px;
        color: #333;
        font-weight: 700;
      }
      &:nth-child(2) {
        color: $colorSoftGray;
        font-size: 12px;
        text-align: right;
      }
    }
  }
  .itemDetail {
    li {
      &:nth-child(1) {
        font-size: 12px;
        color: $colorDarkGray;
        font-weight: 700;
      }
      &:nth-child(2) {
        color: $colorSoftGray;
        font-size: 12px;
      }
    }
  }
  .stock {
    font-size: 11px;
    color: $colorSoftGray;
    font-weight: 700;
  }
  img {
    width: 250px;
  }
}
